import * as types from '../constants/technologyTypes';

const initState = {
  AllTechnologies: [],
  Error: {}
};

const TechnologyReducer = (state = initState, action) => {
  switch (action.type) {
    case types.GET_ALL_TECHNOLOGIES:
      return {
        ...state,
        AllTechnologies: action.datas
      };
      case types.CREATE_TECHNOLOGY:
        return {
          ...state,
          AllTechnologies: [...state.AllTechnologies,action.data]
        }
      case types.DELETE_TECHNOLOGY:
        return  {
          ...state,
          AllTechnologies: state.AllTechnologies.filter(item => item.id !== action.id)
        }
    default:
      return state;
  } 
};

export default TechnologyReducer;
