import React, { Component } from 'react';
import styles from './ProfileList.module.css';
import Table from '../../../common/Table/Table';
import Filter from '../../../../containers/FilterFormView';
import RegistrationFormView from '../../../../containers/RegistrationFormView';
import { paginate } from '../../../../utils/paginate';
import Pagination from '../../../common/pagination/Pagination';
class ProfileList extends Component {
  state = {
    filtered: JSON.parse(localStorage.getItem('filterValues')) || 'clear',
    creating: false,
    drawFilter: false,
    name: '',
    phone: '',
    position: '',
    profile: '',
    portfolio: '',
    comment: '',
    english: 'no english',
    salary: '',
    status: 'Wrote on LinkedIn',
    source: 'LinkedIn',
    recommender: 'me',
    black_list: false,
    pageSize: 100,
    currentPage: 1,
    columnHeaders: [
      { title: 'Name', name: 'name' },
      { title: 'Phone', name: 'phone' },
      { title: 'Current Position', name: 'position' },
      { title: 'Profile', name: 'profile' },
      { title: 'Portfolio', name: 'portfolio' },
      { title: 'Technologies', name: 'technologies' },
      { title: 'English', name: 'english' },
      { title: 'Salary Expectation', name: 'salary' },
      { title: 'Source', name: 'source' },
      { title: 'Status', name: 'status' },
      { title: 'Projects', name: 'projects' },
      { title: 'Comment', name: 'comment' },
      { title: 'Date', name: 'created_at' }
    ]
  };

  componentDidMount() {
    this.props.GetAllProfiles();
  }

  handlePageChange = page => {
    this.setState({ currentPage: page });
  };

  render() {
    let data = [...this.props.AllProfiles];

    const profiles = paginate(
      this.state.filtered === 'clear' ? this.props.AllProfiles : this.state.filtered,
      this.state.currentPage,
      this.state.pageSize
    );

    return (
      <>
        {this.props.showFilter === true ? <Filter form={'filter'} /> : null}
        {this.props.showRegister === true ? <RegistrationFormView form={'register'} /> : null}
        <div className={styles.searchBox} />
        <div className={styles.contFlexBTN} />
        <Table
          data={data}
          showFilter={this.props.showFilter}
          showRegister={this.props.showRegister}
          toggleFilter={this.props.toggleFilter}
          toggleRegister={this.props.toggleRegister}
          GetAllProfiles={this.props.GetAllProfiles}
          columnHeaders={this.state.columnHeaders}
          rows={profiles}
        />
        <Pagination
          itemsCount={this.state.filtered === 'clear' ? data.length : this.state.filtered.length}
          pageSize={this.state.pageSize}
          currentPage={this.state.currentPage}
          onPageChange={this.handlePageChange}
        />
      </>
    );
  }
}
export default ProfileList;
