import { connect } from 'react-redux';
import ProjectItem from '../components/Home/ContentPages/Projects/ProjectItem/ProjectItem';
import { GetAllProjects } from '../actions/ProjectActions';
import { GetAllUsers } from '../actions/ManageUsersActions';
const mapDispatchToProps = dispatch => {
  return {
    GetAllProjects: () => {
      dispatch(GetAllProjects());
    },
    GetAllUsers: () => {
      dispatch(GetAllUsers());
    }
  };
};

const mapStateToProps = state => {
  return {
    AllProjects: state.ProjectReducer.AllProjects,
    currentProject: state.ProjectReducer.AllProjects,
    AllUsers: state.ManageUserReducer.AllUsers
  };
};

const ProjectItemView = connect(
  mapStateToProps,
  mapDispatchToProps
)(ProjectItem);

export default ProjectItemView;
