import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { connect } from 'react-redux';

import LoginView from './containers/LoginView';
import RecoverAccount from './components/RecoverAccount/RecoverAccount';
import HomeView from './containers/HomeView';
import PrivatateRoute from './components/Home/PrivateRoute/PrivateRoute';
import PublicRoute from './components/Home/PublicRoute/PublicRoute';
import NotFoundPage from './components/NotFoundPage/NotFoundPage';
import { userFalse } from './actions/LoginAction';
import { setUser } from './actions/LoginAction';

import './App.css';

class App extends Component {
  state = {};

  static getDerivedStateFromProps(props) {
    let user = localStorage.getItem('user');
    if (user && !props.Authenticated) {
      user = JSON.parse(user);
      props.setUser(user);
    }
    return null;
  }

  render() {
    return (
      <div>
        <Router>
          <Switch>
            <PublicRoute path="/recover-account" component={RecoverAccount} />
            <PrivatateRoute path="/home" component={HomeView} />
            {/* <PublicRoute exact path="/project/:id" component={LoginView} /> */}
            <PublicRoute exact path="/" component={LoginView} />
            <Route component={NotFoundPage} />
          </Switch>
        </Router>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    User: state.UserReducer.User,
    Authenticated: state.UserReducer.Authenticated,
    AllProfiles: state.UserReducer.AllProfiles
  };
};

const mapDispatchToProps = dispatch => {
  return {
    userFalse: user => {
      dispatch(userFalse(user));
    },
    setUser: user => {
      dispatch(setUser(user));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(App);
