import React, { Component } from 'react';
import styles from './ProjectItem.module.css';
import Table from '../../../../common/Table/Table';
import CreateProjectView from '../../../../../containers/CreateProjectView';

export default class ProjectItem extends Component {
  state = {
    show: false,
    updating: false,
    updatingProject: null
  };
  componentDidMount() {
    this.props.GetAllProjects();
    this.props.GetAllUsers();
  }

  handleProjectUpdate = project => {
    this.setState({ updating: !this.state.updating });
    this.setState({ updatingProject: project });
  };
  render() {
    const myProject = this.props.AllProjects.find(item => {
      return parseInt(item.id) === parseInt(this.props.match.params.id);
    });
    if (myProject) {
      var data = [...myProject.profiles];
    }
    const user = this.props.AllUsers;

    if (user && myProject) {
      var author = user.find(x => {
        return x.id == myProject.author_id;
      });
      var updater = user.find(x => {
        return x.id == myProject.updater_id;
      });
    }

    return (
      <div className={styles.container}>
        <div className="ml-4">
          {this.state.updating && (
            <CreateProjectView
              hide={this.handleProjectUpdate}
              name="project"
              show={this.state.updating}
              action="editing"
              project={this.state.updatingProject}
            />
          )}

          {myProject ? (
            <>
              <p>
                <strong>Title : </strong>
                {myProject.title}
              </p>
              {myProject.profiles.length != 0 && (
                <p>
                  <strong>Profiles Count : </strong>
                  {myProject.profiles.length}
                </p>
              )}
              {myProject.status != null && (
                <p>
                  {' '}
                  <strong> Status : </strong> {myProject.status}
                </p>
              )}
              {myProject.referral != null && (
                <p>
                  {' '}
                  <strong> Brought by : </strong> {myProject.referral}
                </p>
              )}
              {myProject.start_date != null && (
                <p>
                  <strong> From : </strong>
                  {myProject.start_date}
                  <strong> To </strong>
                  {myProject.end_date}
                </p>
              )}
              {myProject.comment != null && (
                <p>
                  <strong> Comment :</strong> {myProject.comment}
                </p>
              )}

              {myProject.author_id != null && (
                <p>
                  <strong> Added By :</strong> {author && author.name}
                </p>
              )}
              {myProject.updater_id != null && (
                <p>
                  <strong> Updated By :</strong> {updater && updater.name}
                </p>
              )}
            </>
          ) : null}
          <button className="btn btn-warning mt-2 mb-2" onClick={() => this.handleProjectUpdate(myProject)}>
            Edit
          </button>
        </div>
        <Table className={styles.table} rows={data} onProjectDetail={true} />
      </div>
    );
  }
}
