import axios from '../utils/axios';
import * as types from '../constants/profileTypes';

export const GetAllProfiles = () => {
  return async dispatch => {
    try {
    let AllProfiles = await axios.get('all-profiles');
    dispatch({ type: types.GET_ALL_PROFILES, data: AllProfiles.data.profiles });
    } catch (err) {
    console.log('Error', err);
    // dispatch({ type: types.GET_ALL_PROFILES, data: 'SOMETING WENT WRONG' });
    }
  };
};

export const FilterUsers = filteredUser => ({
  type: types.SET_FILTERED_PROFILES,
  data: filteredUser
});

export const addProfile = (newUser) => {
  return async dispatch => {
    let formData = { ...newUser };
    formData.projects = formData.projects.map(projet => projet.id);
    formData.technologies = formData.technologies.map(technology => technology.id);
    try {
      let addUser = await axios.post('/store-profile', { ...formData });
      let newUser = {
        ...addUser.data.profile,
        projects: addUser.data.projects,
        technologies: addUser.data.technologies
      };
      dispatch({ type: types.ADD_PROFILE, data: newUser });
    } catch (err) {
      dispatch({ type: types.ADD_PROFILE_ERROR, data: err.response.data.error });
    }
  };
};

export const updateCurrentProfile = (user, id) => {
  return async dispatch => {
    try {
      let currentUser = { ...user };
      if(currentUser.projects)
       {currentUser.projects = currentUser.projects.map(project => project.id)}
      if(currentUser.technologies) {currentUser.technologies = currentUser.technologies.map(technology => technology.id)};
      console.log(id,'actionid')
      await axios.put(`/update-profile/${id}`, currentUser);
      // let UpdatedUser = {
      //   ...updatedUser.data.profile,
      //   projects: updatedUser.data.projects,
      //   technologies: updatedUser.data.technologies
      // };
      let AllProfiles = await axios.get('all-profiles');
      dispatch({ type: types.GET_ALL_PROFILES, data: AllProfiles.data.profiles });
    } catch (err) {
      dispatch({ type: types.EDIT_PROFILE_ERROR, data: err.response.statusText });
    }
  };
};
export const toggleRegister = status => {
  return {
    type: types.TOGGLE_REGISTER,
    status: status
  };
};
export const toggleFilter = status => {
  return {
    type: types.TOGGLE_FILTER,
    status: status
  };
};

export const DeleteProfile = id => {
  return async dispatch => {
    try {
      await axios.delete(`destroy-profile/${id}`);
      dispatch({ type: types.DELETE_PROFILE, id: id });
    } catch (err) {
      console.log('error', err);
    }
  };
};

export const UploadFile = files => {
  return async dispatch => {
    let filesData = files;
    try {
      let newFile = await axios.post('/store-file', filesData, {
        headers: { 'Content-Type': 'multipart/form-data' }
      })
      dispatch({ type: types.UPLOAD_FILE, files: [newFile.data.success] });
    } catch (err) {
      console.log(err);
    }
  };
};

export const GetFiles = id => {
  return async dispatch => {
    try {
      let files = await axios.get(`/file/${id}`);
      dispatch({ type: types.GET_FILES, files: files.data.file });
    } catch (err) {
      console.log(err);
    }
  };
};

