import axios from '../utils/axios';
import * as types from '../constants/technologyTypes';

export const GetAllTechnologies = () => {
  return async dispatch => {
    try {
      let Technologies = await axios.get('/get-technologies');
      dispatch({ type: types.GET_ALL_TECHNOLOGIES, datas: Technologies.data.technologies });
    } catch (err) {
      console.log(err);
    }
  };
};

export const CreateTechnology = (newTech) => {
  return async dispatch => {
    let technologyData = { ...newTech };
    try {
      let addTechnology = await axios.post('/store-technology', { ...technologyData });
      dispatch({ type: types.CREATE_TECHNOLOGY, data: addTechnology.data.technology });
    } catch (err) {
      console.log(err);
    }
  };
};

export const DeleteTechnology = id => {
  return async dispatch => {
    try{
      await axios.delete(`/destroy-technology/${id}`)
      dispatch({ type: types.DELETE_TECHNOLOGY, id: id });
    } catch (err) {
      console.log(err);
    }
  }
}