import { connect } from 'react-redux';
import CreateUser from '../components/Home/CreateUser/CreateUser';
// import { userRegistration } from '../actions/CreateUserActions';
import { userFinalUpdate, userUpdateCancel, userRegistration } from '../actions/ManageUsersActions';

const mapDispatchToProps = dispatch => {
  return {
    userRegistration: user => {
      dispatch(userRegistration(user));
    },
    userFinalUpdate: (user, id) => {
      dispatch(userFinalUpdate(user, id));
    },
    userUpdateCancel: () => {
      dispatch(userUpdateCancel());
    }
  };
};

const mapStateToProps = state => {
  return {
    AllProjects: state.ProjectReducer.AllProjects,
    Error: state.ManageUserReducer.Error,
    UpdatingUser: state.ManageUserReducer.UpdatingUser
  };
};

const CreateUserView = connect(
  mapStateToProps,
  mapDispatchToProps
)(CreateUser);

export default CreateUserView;
