import React, { Component } from 'react';
import Joi from '@hapi/joi';
import Select from 'react-select';
import styles from './Form.module.css';
import moment from 'moment';
import isEqual from 'lodash.isequal';
import isEmpty from 'lodash.isempty';
moment().format();

const customStyles = {
  indicatorsContainer: () => ({
    display: 'flex'
  }),
  control: () => ({
    display: 'flex',
    flexDirection: 'row',
    width: 230,
    fontSize: '10px'
  })
};
class Filter extends Component {
  state = {
    projects: [],
    name: '',
    profile: '',
    phone: '',
    technologies: [],
    english: '',
    minSalary: '',
    maxSalary: '',
    salary: '',
    status: '',
    source: '',
    fromdateRange: '1970-01-02',
    todateRange: '',
    errors: {},
    position: '',
    filteredUsers: [],
    projectOptions: [],
    author_id: this.props.User.id
  };
  showFilter = () => {
    this.props.toggleFilter();
    this.props.toggleRegister(false);
  };
  showRegister = () => {
    this.props.toggleFilter(false);
    this.props.toggleRegister();
  };
  getProjectOptionsForSelect = () => {
    return this.props.AllProjects.map((project, i) => {
      return {
        value: project.title,
        id: project.id,
        label: project.title
      };
    });
  };

  getTechnologiesOptionsForSelect = () => {
    return this.props.AllTechnologies.map((technology, i) => {
      return {
        value: technology.title,
        id: technology.id,
        label: technology.title
      };
    });
  };

  filterSchema = {
    projects: Joi.array()
      .min(0)
      .allow('')
      .allow(null)
      .label('project')
      .optional(),

    profile: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .label('profile')
      .optional(),

    phone: Joi.number()
      .min(0)
      .allow('')
      .allow(null)
      .label('phone')
      .optional(),

    technologies: Joi.array()
      .min(0)
      .allow('')
      .allow(null)
      .label('technologies')
      .optional(),

    english: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .label('english')
      .optional(),

    minSalary: Joi.string()
      .min(0)
      .allow('')
      .allow('string')
      .allow(null)
      .label('salary')
      .optional(),

    maxSalary: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .label('salary')
      .optional(),

    status: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .label('status')
      .optional(),

    source: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .label('source')
      .optional(),

    fromdateRange: Joi.date()
      .min(0)
      .allow('')
      .allow(null)
      .label('dateRange')
      .optional(),

    todateRange: Joi.date()
      .min(0)
      .allow('')
      .allow(null)
      .label('dateRange')
      .optional(),
    token: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .label('datetokenRange')
      .optional()
  };

  handleChange = event => {
    event.preventDefault();
    this.setState({ [event.target.name]: event.target.value });
  };

  handleProjectSelectChange = projects => {
    this.setState({ projects: projects });
  };

  handleTechnologiesSelectChange = technologies => {
    this.setState({ technologies: technologies });
  };
  clearFilter = () => {
    this.setState((prevState, props) => {
      return {
        profile: '',
        projects: [],
        phone: '',
        technologies: [],
        english: '',
        status: '',
        source: '',
        fromdateRange: '1970-01-02',
        todateRange: ''
      };
    });
  };
  onSubmit = event => {
    event.preventDefault();
    let user = this.props.User;
    const {
      projects,
      profile,
      phone,
      technologies,
      english,
      minSalary,
      maxSalary,
      status,
      source,
      fromdateRange,
      name,
      position,
      todateRange,
      salary,
      comment,
      author_id
    } = this.state;

    const filteredUser = {
      projects: projects,
      profile: profile,
      phone: phone,
      technologies: technologies,
      english: english,
      minSalary: minSalary,
      maxSalary: maxSalary,
      status: status,
      source: source,
      fromdateRange: fromdateRange,
      todateRange: todateRange,
      token: user.token
    };
    const registeredUser = {
      projects: projects,
      name: name,
      position: position,
      profile: profile,
      phone: phone,
      technologies: technologies,
      english: english,
      salary: salary,
      status: status,
      source: source,

      comment: comment,
      author_id: author_id
    };
    const { error } =
      this.props.form === 'filter' ? Joi.validate(filteredUser, this.filterSchema) : Joi.validate(registeredUser, this.registrationSchema);

    if (error) {
      this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
    } else {
      if (this.props.form === 'filter') {
        let users = this.props.AllProfiles.filter(profile => {
          return profile.name && filteredUser.profile
            ? profile.name
                .trim()
                .toLowerCase()
                .includes(filteredUser.profile.trim().toLowerCase())
            : true && (profile.phone !== null && filteredUser.phone)
            ? profile.phone
                .trim()
                .toLowerCase()
                .replace(/\s/g, '')
                .includes(
                  filteredUser.phone
                    .trim()
                    .toLowerCase()
                    .replace(/\s/g, '')
                )
            : true && (profile.english !== undefined && filteredUser.english)
            ? profile.english === filteredUser.english
            : true && (profile.status && filteredUser.status)
            ? profile.status
                .trim()
                .toLowerCase()
                .includes(filteredUser.status.trim().toLowerCase())
            : true && (profile.source && filteredUser.source)
            ? profile.source
                .trim()
                .toLowerCase()
                .includes(filteredUser.source.trim().toLowerCase())
            : true &&
              (filteredUser.projects.length
                ? profile.projects.filter(project => {
                    return filteredUser.projects.map(option => option.id).includes(project.id);
                  }).length
                : true) &&
              (filteredUser.technologies.length
                ? profile.technologies.filter(technology => {
                    return filteredUser.technologies.map(tec => tec.id).includes(technology.id);
                  }).length
                : true) &&
              (filteredUser.fromdateRange && filteredUser.todateRange
                ? moment(profile.created_at.slice(0, 10)).isBetween(moment(filteredUser.fromdateRange), moment(filteredUser.todateRange))
                : true);
        });
        console.log(users);
        this.props.filterUsers(users);
      } else if (this.props.form === 'register') {
        this.props.addProfile(registeredUser, this.props.User.token);
      }
    }
  };

  componentDidMount() {
    this.props.GetAllProjects();
    this.props.GetAllTechnologies();
  }

  componentDidUpdate(prevProps) {
    const errors = this.props.Error;
    if (!isEqual(prevProps.Error, errors) && !isEmpty(errors)) {
      this.registrationErrors(errors);
    } else if (!isEqual(prevProps.Error, errors) && isEmpty(errors)) {
      this.setState({ errors: {} });
    }
  }

  render() {
    return (
      <>
        <div>
          <form onSubmit={event => this.onSubmit(event)} className={styles.formContainer}>
            <div className={styles.formContainer__item}>
              <h1 className={styles.title}>{this.props.form}</h1>
              <button className={styles.xBtn} onClick={this.showFilter} />
            </div>
            <div className={styles.error}>
              {Object.keys(this.state.errors).map(error => (
                <span key={error}>{this.state.errors[error]}</span>
              ))}
            </div>
            <div className="form-group">
              <input
                placeholder="Profile"
                className="form-control mb-1"
                type="text"
                onChange={event => this.handleChange(event)}
                name="profile"
                value={this.state.profile}
                error={this.state.errors.profile}
              />
            </div>
            <div className="form-group">
              <label htmlFor="projects" className={styles.label}>
                Project
              </label>

              <Select
                styles={customStyles}
                isMulti
                onChange={this.handleProjectSelectChange}
                name="projects"
                value={this.state.projects}
                error={this.state.errors.projects}
                options={this.getProjectOptionsForSelect()}
              />
            </div>
            <div className="form-group">
              <input
                placeholder="Phone"
                className="form-control mb-1"
                type="text"
                onChange={event => this.handleChange(event)}
                name="phone"
                value={this.state.phone}
                error={this.state.errors.phone}
              />
            </div>
            <div className="form-group">
              <label htmlFor="technologies" className={styles.label}>
                Technologies
              </label>

              <Select
                styles={customStyles}
                isMulti
                onChange={this.handleTechnologiesSelectChange}
                name="technology"
                value={this.state.technologies}
                error={this.state.technologies}
                options={this.getTechnologiesOptionsForSelect()}
              />
            </div>
            <div className="form-group">
              <select
                type="text"
                className="form-control mb-1"
                onChange={event => this.handleChange(event)}
                name="english"
                error={this.state.errors.english}
              >
                <option value={''}>English</option>
                <option type="checkbox" value={this.state.english} checked>
                  no english
                </option>
                <option type="checkbox" value="good" checked>
                  good
                </option>
                <option type="checkbox" value="fluent" checked>
                  fluent
                </option>
              </select>
            </div>
            <div className="form-group">
              <select
                className="form-control mb-1"
                type="text"
                onChange={event => this.handleChange(event)}
                name="status"
                error={this.state.errors.status}
                multiple
              >
                <option value={''}>Status</option>
                <option type="checkbox" value="linkedin" checked>
                  linkedin
                </option>
                <option type="checkbox" value="interested" checked>
                  interested
                </option>
                <option type="checkbox" value="rejected" checked>
                  rejected
                </option>
                <option type="checkbox" value="shortlisted" checked>
                  shortlisted
                </option>
                <option type="checkbox" value="hired" checked>
                  hired
                </option>
              </select>
            </div>
            <div className="form-group">
              <select
                type="text"
                className="form-control mb-1"
                onChange={event => this.handleChange(event)}
                name="source"
                value={this.state.source}
                error={this.state.errors.source}
              >
                <option value={''}>Source</option>
                <option type="checkbox" value="refference" checked>
                  refference
                </option>
                <option type="checkbox" value="linkedin" checked>
                  linkedin
                </option>

                <option type="checkbox" value="job post" checked>
                  job post
                </option>
              </select>
            </div>
            <div className="form-group">
              <input
                type="date"
                className="form-control mb-1"
                onChange={event => this.handleChange(event)}
                name="fromdateRange"
                value={this.state.fromdateRange}
                error={this.state.errors.fromdateRange}
              />
              <input
                className="form-control mb-1"
                type="date"
                onChange={event => this.handleChange(event)}
                name="todateRange"
                value={this.state.todateRange}
                error={this.state.errors.todateRange}
              />
            </div>
            <div className="form-group">
              <button type="submit" className="btn btn-success">
                {this.props.form}
              </button>
              <button className="btn btn-warning ml-5" onClick={this.clearFilter}>
                clear
              </button>
            </div>
          </form>
        </div>
      </>
    );
  }
}

export default Filter;
