import React, { Component } from 'react';
import jsPDF from 'jspdf';
import 'jspdf-autotable';

import './Export.css';

class ExportPdf extends Component {
  render() {
    const columns = [
      { title: 'Project', dataKey: 'projects' },
      { title: 'Profile', dataKey: 'name' },
      { title: 'Position', dataKey: 'position' },
      { title: 'Phone', dataKey: 'phone' },
      { title: 'LinkedIn', dataKey: 'profile' },
      { title: 'Portfolio', dataKey: 'portfolio' },
      { title: 'Comment', dataKey: 'comment' },
      { title: 'Technologies', dataKey: 'technologies' },
      { title: 'English', dataKey: 'english' },
      { title: 'Expected Salary', dataKey: 'salary' },
      { title: 'Source', dataKey: 'source' },
      { title: 'Status', dataKey: 'status' },
      { title: 'Date', dataKey: 'created_at' },
    ];
    const rows = this.props.profiles.map(profile => profile)

    const createPdf = () => {
      var doc = new jsPDF({
        orientation: "landscape",
        format: [1200, 1200]
      });
  
      doc.autoTable(columns, rows, { styles: {
        minCellWidth: 30,
        halign: "center"
      }});
      doc.save('profiles.pdf');
    };
    return (
      <button className="export" onClick={createPdf}>
        Pdf
      </button>
    );
  }
}

export default ExportPdf;
