import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import styles from './Projects.module.css';
import CreateProjectView from '../../../../containers/CreateProjectView';
import PermissionRuler from '../../../../hoc/PermissionRuler';
import Button from '../../../common/Button/Button';
import CreateIcon from '../../../common/Table/CreateIcon';
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory from 'react-bootstrap-table2-paginator';

class Projects extends Component {
  state = {
    show: false,
    updating: false,
    updatingProject: null
  };

  handleShowHide = () => {
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };
  handleProjectUpdate = project => {
    this.setState({ updating: !this.state.updating });
    this.setState({ updatingProject: project });
  };

  componentDidMount() {
    this.props.GetAllProjects();
  }

  handleDelete = id => {
    this.props.DeleteProject(id);
  };

  priceFormatter(cell) {
    return (
      <Link to={`project/${cell}`} className="btn btn-primary">
        View
      </Link>
    );
  }
  deleteView = id => {
    return (
      <button onClick={() => this.handleDelete(id)} className="btn btn-danger">
        Delete
      </button>
    );
  };
  editView = id => {
    var project = this.props.AllProjects.filter(project => {
      return project.id == id;
    });
    return (
      <button onClick={() => this.handleProjectUpdate(project[0])} className="btn btn-warning">
        Edit
      </button>
    );
  };

  render() {
    const columns = [
      {
        dataField: 'title',
        text: 'Title',
        sort: true
      },
      {
        dataField: 'status',
        text: 'Status',
        sort: true
      },
      {
        dataField: 'comment',
        text: 'Comment'
      },
      {
        dataField: 'referral',
        text: 'Referral',
        sort: true
      },
      {
        dataField: 'start_date',
        text: 'Start Date',
        sort: true
      },
      {
        dataField: 'end_date',
        text: 'End Date',
        sort: true
      },
      {
        dataField: 'id',
        text: 'Edit',
        sort: true,
        formatter: this.editView
      },
      {
        dataField: 'id',
        text: 'Delete',
        sort: true,
        formatter: this.deleteView
      },

      {
        dataField: 'id',
        text: 'View',
        sort: true,
        formatter: this.priceFormatter
      }
    ];

    return (
      <div className={styles.project_conteiner}>
        <PermissionRuler roles={['admin', 'manager']}>
          <Button onClick={this.handleShowHide} className="button">
            Create Project &nbsp;&nbsp; <CreateIcon />
          </Button>
          <CreateProjectView hide={this.handleShowHide} name="project" show={this.state.show} action="creating" />
          {this.state.updating && (
            <CreateProjectView
              hide={this.handleProjectUpdate}
              name="project"
              show={this.state.updating}
              action="editing"
              project={this.state.updatingProject}
            />
          )}
        </PermissionRuler>
        <BootstrapTable data={this.props.AllProjects} columns={columns} keyField="id" pagination={paginationFactory()} className={styles.fullWidth} />
      </div>
    );
  }
}

export default Projects;
