import axios from 'axios';

let configuredAxios = axios.create({
  baseURL: 'https://hrms.techubdc.ge/api',
  headers: {
    common: { 'Content-Type': 'application/json', Accept: 'application/json', Authorization: null }
  }
});

export const setToken = token => {
  configuredAxios.defaults.headers.common.Authorization = `Bearer ${token}`;
};

export const clearToken = () => {
  configuredAxios.defaults.headers.common.Authorization = null;
};

configuredAxios.interceptors.response.use(
  function(res) {
    return res;
  },
  function(error) {
    console.log('response error', error);
    let statusCode = error.response.status;
    if (statusCode === 401) {
      localStorage.clear();
      window.location = '/';
    }
    return Promise.reject(error);
  }
);

export default configuredAxios;
