import { connect } from 'react-redux';
import ProfileItem from '../components/Home/ContentPages/ProfileList/ProfileItem/ProfileItem';
import { GetAllProfiles, DeleteProfile, UploadFile, GetFiles, DeleteFile } from '../actions/ProfilePageActions';
import { GetAllUsers } from '../actions/ManageUsersActions';

const mapStateToProps = (state, props) => {
  return {
    Profile: state.UserReducer.AllProfiles.find(profile => profile.id === parseInt(props.match.params.id)),
    User: state.UserReducer.User,
    files: state.UserReducer.files,
    AllUsers: state.ManageUserReducer.AllUsers
  };
};

const mapDispatchToProps = dispatch => {
  return {
    GetAllProfiles: token => {
      dispatch(GetAllProfiles(token));
    },
    DeleteProfile: id => {
      dispatch(DeleteProfile(id));
    },
    UploadFile: files => {
      dispatch(UploadFile(files));
    },
    GetFiles: id => {
      dispatch(GetFiles(id));
    },
    DeleteFile: id => {
      dispatch(DeleteFile(id));
    },
    GetAllUsers: () => {
      dispatch(GetAllUsers());
    }
  };
};

const ProfileItemView = connect(
  mapStateToProps,
  mapDispatchToProps
)(ProfileItem);

export default ProfileItemView;
