import React from 'react';
import Joi from '@hapi/joi';
import Recaptcha from 'react-recaptcha';
import { Link } from 'react-router-dom';

import Input from '../common/Input/Input';
import Button from '../common/Button/Button';
import FormCard from '../common/FormCard/FormCard';

import styles from './RecoverAccount.module.css';

const sitekey = '6Lf7daYUAAAAAC4ldgNYNcfnupdU4OqnAIERnwgq';

class RecoverAccount extends React.Component {
  state = {
    email: '',
    isVerified: false,
    errors: {}
  };

  schema = {
    email: Joi.string()
      .email()
      .required()
      .label('Email')
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };
  handleSubmit = e => {
    e.preventDefault();

    // Checking email and captcha
    const { error } = Joi.validate({ email: this.state.email }, this.schema);
    if (error) {
      this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
      return;
    }

    if (!this.state.isVerified) {
      this.setState({ errors: { capctcha: 'Capthca is not verified' } });
      return;
    }

    console.log('Here we send request');
  };

  componentWillUnmount() {
    if (this.recaptchaInstance) {
      this.recaptchaInstance.reset();
    }
  }
  handleCaptchaLoad = () => {
    console.log('Loaded Captcha');
  };

  handleVerifyCaptcha = response => {
    if (response) this.setState({ isVerified: true });
  };

  render() {
    return (
      <FormCard>
        <form onSubmit={this.handleSubmit}>
          <Input
            className={styles.checkBox}
            type="email"
            name="email"
            placeholder="Enter Email"
            value={this.state.email}
            error={this.state.errors.email}
            onChange={this.handleChange}
          />

          <Recaptcha
            render="explicit"
            sitekey={sitekey}
            onloadCallback={this.handleCaptchaLoad}
            verifyCallback={this.handleVerifyCaptcha}
            ref={e => (this.recaptchaInstance = e)}
          />

          <div className={styles.error}>
            {Object.keys(this.state.errors).map(error => (
              <p key={error}>{this.state.errors[error]}</p>
            ))}
          </div>
          <nav className={styles.buttonsNav}>
            <Button onClick={this.handleSubmit} className="request">
              Request
            </Button>
            <Button className="backToBtn">
              <Link className={styles.link} to="/">
                Back To Sign In
              </Link>
            </Button>
          </nav>
        </form>
      </FormCard>
    );
  }
}

export default RecoverAccount;
