import { connect } from 'react-redux';
import CreateForm from '../components/Home/CreateForm/CreateForm';
import { CreateProject, EditProject } from '../actions/ProjectActions';

const mapStateToProps = state => {
  return {
    AllProjects: state.ProjectReducer.AllProjects,
    User: state.UserReducer.User,
    CreateProjectErrors: state.ProjectReducer.CreateProjectErrors,
    DeleteProjectError: state.ProjectReducer.DeleteProjectError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    createProject: (project, token) => {
      dispatch(CreateProject(project, token));
    },
    editProject: (project, id, token) => {
      dispatch(EditProject(project, id, token));
    }
  };
};

const CreateProjectView = connect(
  mapStateToProps,
  mapDispatchToProps
)(CreateForm);

export default CreateProjectView;
