import React, { Component } from 'react';
import RegistrationFormView from '../../../../../containers/RegistrationFormView';
import styles from './ProfileItem.module.css';
import Button from '../../../../common/Button/Button';
import PermissionRuler from '../../../../../hoc/PermissionRuler';
class ProfileItem extends Component {
  state = {
    show: false
  };

  showEditUserForm = () => {
    this.setState({ show: !this.state.show });
  };

  handleDeletProfile = id => {
    this.props.DeleteProfile(id);
    this.props.history.replace(`/home`);
  };

  handleUpload = e => {
    let formData = new FormData();
    let file = e.target.files[0];
    formData.append('file', file);
    formData.append('author_id', this.props.User.id);
    formData.append('user_id', this.props.Profile.id);
    // let ragaca = new objectToFormData({
    //   name: 'file',
    //   file: file
    // })
    this.props.UploadFile(formData);
    e.target.value = '';
  };

  handleDeleteFile = id => {
    this.props.DeleteFile(id);
  };

  handleDownload = path => {
    window.open(`http://laravel.local/${path}`, '_blank');
  };

  componentDidMount() {
    if (!this.props.Profile) {
      this.props.GetAllProfiles();
    }
    if (this.props.GetAllUsers) {
      this.props.GetAllUsers();
    }
    if (this.props.files) {
      this.props.GetFiles(this.props.match.params.id);
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.files.length !== this.props.files.length) {
      this.props.GetFiles(this.props.match.params.id);
    }
  }

  handleDeleteProfile = id => {
    this.props.DeleteProfile(id);
    this.props.history.replace('/home');
  };

  parseProfile = profile => {
    if (profile.includes('linkedin')) {
      return 'Linkedin';
    } else if (profile.includes('facebook')) {
      return 'Facebook';
    } else if (profile.includes('drive')) {
      return 'Google Drive';
    } else if (profile.includes('.gl')) {
      return 'Goo.gl';
    } else {
      return profile;
    }
  };

  render() {
    const profile = this.props.Profile;
    const user = this.props.AllUsers;
    if (user && profile) {
      var author = user.find(x => {
        return x.id == profile.author_id;
      });
      var updater = user.find(x => {
        return x.id == profile.updater_id;
      });
    }
    return (
      <>
        {profile ? (
          <div className={styles.row}>
            <div className={styles.col4}>
              <div className={styles.wrap}>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Name:</span>
                  {profile.name}
                </div>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Phone:</span>
                  {profile.phone}
                </div>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Position:</span>
                  {profile.position}
                </div>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Profile:</span>
                  <a target="_blank" href={profile.profile}>
                    {this.parseProfile(profile.profile)}
                  </a>
                </div>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Portfolio:</span>
                  {profile.portfolio}
                </div>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Source:</span>
                  {profile.source}
                </div>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>English:</span>
                  {profile.english}
                </div>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Salary:</span>
                  {profile.salary}
                </div>
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Status:</span>
                  {profile.status}
                </div>
                <div className={styles.listContainer}>
                  <PermissionRuler roles={['admin', 'manager']}>
                    <Button className="filter" onClick={this.showEditUserForm}>
                      Edit Profile
                    </Button>
                    <Button className="delete" onClick={() => this.handleDeleteProfile(this.props.Profile.id)}>
                      Delete Profile
                    </Button>
                    {this.state.show ? (
                      <RegistrationFormView form="edit" showHide={this.showEditUserForm} currentProfile={this.props.Profile} />
                    ) : null}
                  </PermissionRuler>
                </div>
              </div>
            </div>
            {/* </div> */}
            <div className={styles.col6}>
              <div className={styles.wrap}>
                {/* Projects */}
                <div className={styles.box}>
                  <h3>Projects</h3>
                  <div className={styles.userInfoItem}>
                    <div className={styles.listContainer2}>
                      {profile.projects.map((item, i) => (
                        <span key={i} className={styles.listItem}>
                          #{item.title}
                        </span>
                      ))}
                    </div>
                  </div>
                </div>

                {/* Technologies */}
                <div className={styles.box}>
                  <h3>Technologies</h3>
                  <div className={styles.userInfoItem}>
                    <div className={styles.listContainer}>
                      {profile.technologies.map((item, i) => (
                        <span key={i} className={styles.listItem}>
                          #{item.title}
                        </span>
                      ))}
                    </div>
                  </div>
                </div>

                {/* Comment */}
                <div className={styles.box}>
                  <span className={styles.userInfoTitle}>Comment:</span>
                  {profile.comment}
                </div>

                {/* Author */}
                {profile.author_id && (
                  <div className={styles.box}>
                    <span className={styles.userInfoTitle}>Added By:</span>
                    {author && author.name}
                  </div>
                )}

                {/* Updated */}
                {profile.updater_id && (
                  <div className={styles.box}>
                    <span className={styles.userInfoTitle}>Updated By: </span> &nbsp;
                    {updater && updater.name}
                  </div>
                )}
              </div>
            </div>
          </div>
        ) : null}
      </>
    );
  }
}

export default ProfileItem;
