import React, { Component } from 'react';
import styles from './CreateForm.module.css';
import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';
import Modal from '../../common/Modal/Modal';
import FormField from './FormField';

class CreateTechnologyForm extends Component {
  state = {
    title: '',
    error: null
  };

  handleCancel = e => {
    e.preventDefault();
    this.setState({
      title: '',
      error: null
    });
    this.props.hide();
  };

  handleSubmit = e => {
    e.preventDefault();
    const { title } = this.state;
    if (title === '') {
      return this.setState({ error: 'Technology title is Required' });
    } else {
      let newProject = {
        title: title,
        author_id: this.props.User.id
      };
      this.props.createTechnology(newProject, this.props.User.token);

      this.setState({
        title: ''
      });
      this.props.hide();
    }
  };
  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  render() {
    const renderForm = () => {
      return (
        <form className={styles.form} onSubmit={e => this.handleSubmit(e)}>
          <FormField className="form-group">
            <input className="form-control" placeholder="Title" type="text" value={this.state.title} name={'title'} onChange={this.handleChange} />
          </FormField>
          {this.state.error && <p style={{ color: 'red', fontSize: '11px' }}>{this.state.error}</p>}

          <button className="btn btn-success" type="submit">
            Create
          </button>
        </form>
      );
    };

    return (
      <Modal show={this.props.show} onCancel={this.handleCancel}>
        {renderForm()}
      </Modal>
    );
  }
}

export default CreateTechnologyForm;
