import { connect } from 'react-redux';
import Profile from '../components/Home/ContentPages/ProfileList/ProfileList';
import { GetAllProfiles, toggleRegister, toggleFilter } from '../actions/ProfilePageActions';

const mapStateToProps = state => {
  return {
    User: state.UserReducer.User,
    LoginError: state.UserReducer.LoginError,
    Authenticated: state.UserReducer.Authenticated,
    AllProfiles: state.UserReducer.AllProfiles,
    showRegister: state.UserReducer.showRegister,
    showFilter: state.UserReducer.showFilter
  };
};

const mapDispatchToProps = dispatch => {
  return {
    GetAllProfiles: (token) => {
      dispatch(GetAllProfiles(token));
    },
    toggleRegister: status => {
      dispatch(toggleRegister(status));
    },
    toggleFilter: status => {
      dispatch(toggleFilter(status));
    }
  };
};

const ProfileView = connect(
  mapStateToProps,
  mapDispatchToProps
)(Profile);

export default ProfileView;
