import axios from '../utils/axios';

import * as types from '../constants/manageUsersTypes';

export const GetAllUsers = token => {
  return async dispatch => {
    try {
      let Users = await axios.get('/users');
      dispatch({ type: types.GET_ALL_USERS, data: Users.data.users });
    } catch (err) {
      console.log('Error', err);
    }
  };
};

export const DeleteUser = id => {
  return async dispatch => {
    try {
      await axios.delete(`/destroy-user/${id}`);
      let Users = await axios.get('/users');
      dispatch({ type: types.GET_ALL_USERS, data: Users.data.users });
    } catch (error) {
      dispatch({ type: 'DELETE_USERS_ERROR', data: error });
    }
  };
};

export const EditUser = user => {
  return { type: types.UPDATE_USER, data: user };
};

export const userFinalUpdate = (user, id) => {
  return async dispatch => {
    try {
      await axios.put(`/update-user/${id}`, user);
      let Users = await axios.get('/users');
      dispatch({ type: types.FINAL_UPDATE_USER, data: Users.data.users });
    } catch (error) {
      return {
        type: types.FINAL_UPDATE_USER_ERROR,
        data: 'something went wrong try again'
      };
    }
  };
};

export const userUpdateCancel = () => {
  return { type: types.USER_UPDATE_CANCEL };
};

export const userRegistration = user => {
  return async dispatch => {
    try {
      await axios.post('/register', user);
      let Users = await axios.get('/users');
      dispatch({ type: types.GET_ALL_USERS, data: Users.data.users });
    } catch (err) {
      dispatch({ type: types.CREATE_USER_ERROR, data: err.response.data.error });
    }
  };
};