import React, { Component } from 'react';
import Button from '../../../common/Button/Button';
import styles from './Analytics.module.css';
import { Bar } from 'react-chartjs-2';
import color from 'rcolor';

class Analytics extends Component {
  state = {};
  componentDidMount() {
    this.props.GetAllProjects();
  }

  handleChoose = e => {
    e.preventDefault();
    var projects = this.props.AllProjects;
    var project = projects.filter(item => {
      return parseInt(item.id) === parseInt(e.target.select.value);
    });
    this.setState({ currentProject: project[0] });
  };
  render() {
    if (this.state.currentProject) {
      var profiles = this.state.currentProject.profiles;
      var linkedin = profiles.filter(item => {
        return item.status === 'wrote on linkedin';
      });
      var refused = profiles.filter(item => {
        return item.status === 'refused';
      });
      var interested = profiles.filter(item => {
        return item.status === 'interested';
      });
      var rejected = profiles.filter(item => {
        return item.status === 'rejected';
      });
      var shortlisted = profiles.filter(item => {
        return item.status === 'shortlisted';
      });
      var hired = profiles.filter(item => {
        return item.status === 'hired';
      });
    }
    const data = {
      labels: ['Wrote on linkedin', 'Refused', 'Interested', 'Rejected', 'Shortlisted', 'Hired'],
      datasets: [
        {
          label: this.state.currentProject ? this.state.currentProject.title : 'No Selected Project',
          backgroundColor: [color(), color(), color(), color(), color()],
          data: [
            linkedin ? linkedin.length : 0,
            refused ? refused.length : 0,
            interested ? interested.length : 0,
            rejected ? rejected.length : 0,
            shortlisted ? shortlisted.length : 0,
            hired ? hired.length : 0
          ]
        }
      ]
    };
    return (
      <>
        <div>
          <form onSubmit={this.handleChoose} className={styles.selectBox}>
            <select className={styles.select} name="select">
              <option value="select">Choose Project</option>
              {this.props.AllProjects ? (
                <>
                  {this.props.AllProjects.map(project => {
                    return (
                      <option key={project.id} value={project.id}>
                        {project.title}
                      </option>
                    );
                  })}
                </>
              ) : null}
            </select>
            <Button className="choose" type="submit">
              Submit
            </Button>
          </form>
          <Bar
            data={data}
            height={120}
            options={{
              maintainAspectRatio: true,
              scales: {
                yAxes: [
                  {
                    ticks: {
                      beginAtZero: true
                    }
                  }
                ]
              }
            }}
          />
        </div>
      </>
    );
  }
}

export default Analytics;
