import React, { Component } from 'react';
import styles from './Technologies.module.css';
import CreateTechnologyView from '../../../../containers/CreateTechnologyView';
import PermissionRuler from '../../../../hoc/PermissionRuler';
import Button from '../../../common/Button/Button';
import { Link } from 'react-router-dom';
class Technologies extends Component {
  state = {
    show: false,
    technology: {}
  };
  handleShowHide = () => {
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };

  componentDidMount() {
    this.props.GetAllTechnologies();
  }

  handleDelete = id => {
    this.props.DeleteTechnology(id);
  };

  render() {
    console.log(this.props);
    return (
      <>
        <div className={styles.technologies_item}>
          <PermissionRuler roles={['admin', 'manager']}>
            {!this.state.show ? (
              <Button onClick={this.handleShowHide} className="button">
                Create
              </Button>
            ) : null}
            <CreateTechnologyView hide={this.handleShowHide} name="technology" show={this.state.show} />
          </PermissionRuler>

          <div className="col-md-12 mt-3">
            <div className="card table-card">
              <div className="card-header">
                <h5>Technologies</h5>
              </div>
              <div className="card-block p-b-0">
                <div className="table-responsive">
                  <table className="table table-hover m-b-0 text-center table-striped table-bordered table-sm" cellSpacing="0">
                    <thead>
                      <tr>
                        <th>Title</th>
                        <th>Profiles</th>

                        <th>Delete</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.props.AllTechnologies
                        ? this.props.AllTechnologies.map((technology, i) => {
                            return (
                              <tr key={i}>
                                <td>
                                  <Link to={`/home/technologies/${technology.id}`}>{technology.title}</Link>
                                </td>
                                <td>{technology.profiles && technology.profiles.length}</td>
                                <td>
                                  <PermissionRuler roles={['admin', 'manager']}>
                                    <button className="btn btn-danger" value={technology.id} onClick={() => this.handleDelete(technology.id)}>
                                      Delete
                                    </button>
                                  </PermissionRuler>
                                </td>
                              </tr>
                            );
                          })
                        : null}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Technologies;
