import * as userTypes from '../constants/loginTypes';
import * as profTypes from '../constants/profileTypes';

const initState = {
  User: {},
  Authenticated: false,
  LoginError: '',
  AllProfiles: [],
  EditProfileError: [],
  Error: {},
  showRegister: false,
  showFilter: false,
  files: []
};

const UserReducer = (state = initState, action) => {
  switch (action.type) {
    case userTypes.USER_LOGIN:
      return {
        ...state,
        User: action.datas,
        Authenticated: action.datas.status
      };
    case userTypes.USER_LOGOUT:
      return {
        ...state,
        User: {},
        Authenticated: false
      };
    case profTypes.ADD_PROFILE:
      return {
        ...state,
        AllProfiles: [...state.AllProfiles, action.data],
        showRegister: false
      };
    case profTypes.ADD_PROFILE_ERROR:
      return {
        ...state,
        Error: action.data
      };
    case profTypes.EDIT_PROFILE:
      return {
        ...state,
        AllProfiles: [...state.AllProfiles, action.data]
      };
    case profTypes.EDIT_PROFILE_ERROR:
      return {
        ...state,
        EditProfileError: [action.data]
      };
    case userTypes.WRONG_USER:
      return {
        ...state,
        LoginError: action.datas.msg,
        Authenticated: action.datas.status
      };
    case userTypes.SET_USER:
      return {
        ...state,
        User: action.user,
        Authenticated: true
      };
    case profTypes.GET_ALL_PROFILES:
      return {
        ...state,
        AllProfiles: action.data
      };
    case profTypes.SET_FILTERED_PROFILES:
      return {
        ...state,
        AllProfiles: action.data
      };
    case profTypes.TOGGLE_REGISTER:
      return {
        ...state,
        showRegister: typeof action.status !== 'undefined' ? action.status : !state.showRegister
      };
    case profTypes.TOGGLE_FILTER:
      return {
        ...state,
        showFilter: typeof action.status !== 'undefined' ? action.status : !state.showFilter
      };
    case profTypes.DELETE_PROFILE:
      let filteredProfiles = state.AllProfiles.filter(item => item.id !== action.id);
      return {
        ...state,
        AllProfiles: filteredProfiles
      };
    case profTypes.UPLOAD_FILE:
      return {
        ...state,
        files: [...state.files, ...action.files]
      };
    case profTypes.GET_FILES:
      return {
        ...state,
        files: action.files
      };
    case profTypes.DELETE_FILE:
      let filteredFiles = state.files.filter(item => item.id !== action.id);
      return {
        ...state,
        files: filteredFiles
      };
    default:
      return state;
  }
};

export default UserReducer;
