import { connect } from 'react-redux';
import Dashboard from '../components/Home/ContentPages/Dashboard/Dashboard';
import { GetAllProfiles } from '../actions/ProfilePageActions';
import { GetAllProjects } from '../actions/ProjectActions';
import { GetAllTechnologies } from '../actions/TechnologyActions';
import { GetAllUsers } from '../actions/ManageUsersActions';

const mapStateToProps = state => {
  return {
    AllProfiles: state.UserReducer.AllProfiles,
    AllProjects: state.ProjectReducer.AllProjects,
    AllTechnologies: state.TechnologyReducer.AllTechnologies,
    AllUsers: state.ManageUserReducer.AllUsers
  };
};

const mapDispatchToProps = dispatch => {
  return {
    GetAllProfiles: token => {
      dispatch(GetAllProfiles(token));
    },
    GetAllProjects: token => {
      dispatch(GetAllProjects(token));
    },
    GetAllTechnologies: token => {
      dispatch(GetAllTechnologies(token));
    },
    GetAllUsers: token => {
      dispatch(GetAllUsers(token));
    }
  };
};

const DashboardView = connect(
  mapStateToProps,
  mapDispatchToProps
)(Dashboard);

export default DashboardView;
