import React, { Component } from 'react';
import styles from './CreateForm.module.css';
import 'react-dates/initialize';
import 'react-dates/lib/css/_datepicker.css';
import Modal from '../../common/Modal/Modal';
import FormField from './FormField';

class CreateForm extends Component {
  state = {
    title: this.props.project ? this.props.project.title : '',
    lead: this.props.project ? this.props.project.referral : '',
    status: this.props.project ? this.props.project.status : '',
    author_id: this.props.User.id,
    startDate: this.props.project ? this.props.project.start_date : null,
    comment: this.props.project ? this.props.project.comment : '',
    endDate: this.props.project ? this.props.project.end_date : null,
    focusedInput: null,
    errors: {},
    action: this.props.action
  };

  componentDidMount() {
    console.log('edit project ', this.props.project);
  }

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { title, author_id, startDate, endDate, lead, status, comment } = this.state;
    console.log(startDate);
    let newOne =
      this.props.action === 'editing'
        ? {
            title: title,
            start_date: startDate,
            end_date: endDate,
            referral: lead,
            status: status,
            comment: comment,
            updater_id: author_id
          }
        : {
            title: title,
            start_date: startDate,
            end_date: endDate,
            referral: lead,
            status: status,
            comment: comment,
            author_id: author_id
          };
    this.state.action === 'editing'
      ? this.props.editProject(newOne, this.props.project.id, this.props.User.token)
      : this.props.createProject(newOne, this.props.User.token);

    this.setState({
      title: '',
      lead: '',
      status: '',
      author_id: this.props.User.id,
      startDate: null,
      endDate: null,
      comment: null,
      focusedInput: null,
      errors: {}
    });
    this.props.hide();
  };

  handleCancel = e => {
    e.preventDefault();
    this.setState({
      title: ''
    });
    this.props.hide();
  };

  render() {
    const renderForm = () => {
      return (
        <form className={styles.form} onSubmit={e => this.handleSubmit(e)}>
          <FormField className="form-group">
            <input className="form-control" placeholder="Title" type="text" value={this.state.title} name={'title'} onChange={this.handleChange} />
          </FormField>
          <FormField className="form-group">
            <input className="form-control" placeholder="Lead" type="text" value={this.state.lead} name={'lead'} onChange={this.handleChange} />
          </FormField>
          <FormField className="form-group">
            <input
              className="form-control"
              placeholder="Comment"
              type="text"
              value={this.state.comment}
              name={'comment'}
              onChange={this.handleChange}
            />
          </FormField>
          <FormField className="form-group">
            <select className="custom-select" name="status" onChange={this.handleChange}>
              <option selected value={this.state.status}>
                {this.state.status}
              </option>
              <option value="Planned">Planned</option>
              <option value="In Progress">In Progress</option>
              <option value="Postponed">Postponed</option>
              <option value="Cancelled">Cancelled</option>
              <option value="Archived">Archived</option>
            </select>
          </FormField>
          <FormField className="form-group">
            <input type="date" className="form-control" name="startDate" value={this.state.startDate} onChange={this.handleChange} />
          </FormField>
          <FormField className="form-group">
            <input type="date" className="form-control" name="endDate" value={this.state.endDate} onChange={this.handleChange} />
          </FormField>
          <button className="btn btn-success" type="submit">
            {this.state.action === 'editing' ? 'Edit ' : 'Create'}
          </button>
        </form>
      );
    };
    return (
      <Modal show={this.props.show} onCancel={this.handleCancel}>
        {renderForm()}
      </Modal>
    );
  }
}

export default CreateForm;
