import React, { Component } from 'react';
import LatestProfiles from './Latest-Profiles';
import LatestProjects from './Latest-Projects';
import LatestTechnologies from './Latest-Technologies';
import styles from './Dashboard.module.css';

class Dashboard extends Component {
  state = {};
  componentDidMount() {
    this.props.GetAllProfiles();
    this.props.GetAllProjects();
    this.props.GetAllTechnologies();
    this.props.GetAllUsers();
  }
  render() {
    return (
      <>
        <div className="row m-5">
          <div className="col-xl-3 col-md-6">
            <div className="card prod-p-card card-red">
              <div className="card-body">
                <div className="row align-items-center m-b-30">
                  <div className="col">
                    <h6 className="m-b-5 text-white">Total Profiles</h6>
                    <h3 className="m-b-0 f-w-700 text-white">{this.props.AllProfiles.length}</h3>
                  </div>
                  <div className="col-auto">
                    <i className="fas fa-money-bill-alt text-c-red f-18" />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="card prod-p-card card-blue">
              <div className="card-body">
                <div className="row align-items-center m-b-30">
                  <div className="col">
                    <h6 className="m-b-5 text-white">Total Projects</h6>
                    <h3 className="m-b-0 f-w-700 text-white">{this.props.AllProjects.length}</h3>
                  </div>
                  <div className="col-auto">
                    <i className="fas fa-database text-c-blue f-18" />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="card prod-p-card card-green">
              <div className="card-body">
                <div className="row align-items-center m-b-30">
                  <div className="col">
                    <h6 className="m-b-5 text-white">Total Technologies</h6>
                    <h3 className="m-b-0 f-w-700 text-white">{this.props.AllTechnologies.length}</h3>
                  </div>
                  <div className="col-auto">
                    <i className="fas fa-dollar-sign text-c-green f-18" />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="card prod-p-card card-yellow">
              <div className="card-body">
                <div className="row align-items-center m-b-30">
                  <div className="col">
                    <h6 className="m-b-5 text-white">Total Users</h6>
                    <h3 className="m-b-0 f-w-700 text-white">{this.props.AllUsers.length}</h3>
                  </div>
                  <div className="col-auto">
                    <i className="fas fa-tags text-c-yellow f-18" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className={styles.LatestBox}>
          <LatestProfiles profiles={this.props.AllProfiles.slice(Math.max(this.props.AllProfiles.length - 5, 1))} />
          <LatestProjects projects={this.props.AllProjects.slice(Math.max(this.props.AllProjects.length - 5, 1))} />
          <LatestTechnologies technologies={this.props.AllTechnologies.slice(Math.max(this.props.AllTechnologies.length - 7, 1))} />
        </div>
      </>
    );
  }
}

export default Dashboard;
