import React, { Component } from 'react';
import Joi from '@hapi/joi';
import CreatableSelect from 'react-select/creatable';
import styles from './Form.module.css';
import moment from 'moment';
import isEqual from 'lodash.isequal';
import isEmpty from 'lodash.isempty';
moment().format();

const customStyles = {
  indicatorsContainer: () => ({
    display: 'flex'
  }),
  control: () => ({
    display: 'flex',
    flexDirection: 'row',
    width: 230,
    fontSize: '10px'
  })
};

class Registration extends Component {
  state = {
    projects: [],
    name: '',
    profile: '',
    phone: '',
    portfolio: '',
    technologies: [],
    english: '',
    salary: '',
    status: '',
    source: '',
    errors: {},
    position: '',
    filteredUsers: [],
    projectOptions: [],
    comment: '',
    author_id: this.props.User.id,
    updater_id: this.props.User.id,
    black_list: this.props.currentProfile ? this.props.currentProfile.black_list : null
  };
  showFilter = () => {
    this.props.toggleFilter();
    this.props.toggleRegister(false);
  };
  showRegister = () => {
    this.props.toggleFilter(false);
    this.props.toggleRegister();
  };
  getProjectOptionsForSelect = () => {
    return this.props.AllProjects.map((project, i) => {
      return {
        value: project.title,
        id: project.id,
        label: project.title,
        key: project.id
      };
    });
  };
  getTechnologiesOptionsForSelect = () => {
    return this.props.AllTechnologies.map((technology, i) => {
      return {
        value: technology.title,
        id: technology.id,
        label: technology.title,
        key: technology.id
      };
    });
  };
  registrationSchema = {
    projects: Joi.array()
      .min(0)
      .label('project')
      .allow('')
      .allow(null)
      .optional(),

    profile: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .optional()
      .label('profile'),
    name: Joi.string()
      .required()
      .min(3)
      .label('name'),
    position: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .optional()
      .label('position'),

    portfolio: Joi.string()
      .min(0)
      .allow('')
      .allow(null)
      .optional()
      .label('portfolio'),

    phone: Joi.string()
      .min(0)
      .allow(null)
      .optional()
      .label('phone'),

    technologies: Joi.array()
      .label('technologies')
      .allow('')
      .allow(null)
      .optional(),

    english: Joi.string()
      .min(0)
      .label('english')
      .allow('')
      .allow(null)
      .optional(),

    salary: Joi.string()
      .min(0)
      .label('salary')
      .allow('')
      .allow(null)
      .optional(),

    status: Joi.string()
      .min(0)
      .label('status')
      .allow('')
      .allow(null)
      .optional(),

    source: Joi.string()
      .min(0)
      .label('source')
      .allow('')
      .allow(null)
      .optional(),
    comment: Joi.string()
      .min(0)
      .label('comment')
      .allow('')
      .allow(null)
      .optional(),
    author_id: Joi.number()
      .min(1)
      .required()
  };
  editSchema = {
    projects: Joi.array()
      .min(0)
      .label('project')
      .allow('')
      .allow(null)
      .optional(),

    profile: Joi.string()
      .min(3)
      .label('profile')
      .allow('')
      .allow(null)
      .optional(),

    name: Joi.string()
      .required()
      .min(3)
      .label('name'),
    position: Joi.string()
      .min(3)
      .label('position')
      .allow('')
      .allow(null)
      .optional(),

    portfolio: Joi.string()
      .min(3)
      .label('portfolio')
      .allow('')
      .allow(null)
      .optional(),

    phone: Joi.string()
      .min(0)
      .label('phone')
      .allow(null)
      .optional(),

    technologies: Joi.array()
      .label('technologies')
      .allow('')
      .allow(null)
      .optional(),

    english: Joi.string()
      .min(3)
      .label('english')
      .allow('')
      .allow(null)
      .optional(),

    salary: Joi.string()
      .min(1)
      .label('salary')
      .allow('')
      .allow(null)
      .optional(),

    status: Joi.string()
      .min(2)
      .label('status')
      .allow('')
      .allow(null)
      .optional(),

    source: Joi.string()
      .min(3)
      .label('source')
      .allow('')
      .allow(null)
      .optional(),
    comment: Joi.string()
      .min(3)
      .label('comment')
      .allow('')
      .allow(null)
      .optional(),
    updater_id: Joi.number()
      .min(1)
      .required()
  };

  handleChange = event => {
    event.preventDefault();
    this.setState({ [event.target.name]: event.target.value });
  };

  handleProjectSelectChange = (newValue, actionMeta) => {
    console.groupEnd();
    this.setState({ projects: newValue });
  };

  handleCreateProjectOption = inputValue => {
    let newOne = {
      title: inputValue,
      author_id: this.state.author_id
    };
    this.props.createProject(newOne, this.props.User.token);
  };

  handleTechnologiesSelectChange = (newValue, actionMeta) => {
    console.groupEnd();
    this.setState({ technologies: newValue });
  };

  handleCreateTechnologyOption = inputValue => {
    let newOne = {
      title: inputValue,
      author_id: this.state.author_id
    };
    this.props.createTechnology(newOne, this.props.User.token);
  };
  clearForm = () => {
    this.setState({
      profile: '',
      position: '',
      portfolio: '',
      projects: [],
      phone: '',
      technologies: [],
      english: '',
      salary: '',
      status: '',
      source: '',
      name: '',
      comment: '',
      errors: {}
    });
  };

  onSubmit = event => {
    event.preventDefault();
    const { projects, profile, phone, technologies, english, status, source, name, position, portfolio, salary, comment, author_id } = this.state;
    const registeredUser = {
      projects: projects ? projects : undefined,
      name: name,
      position: position ? position : undefined,
      portfolio: portfolio ? portfolio : undefined,
      profile: profile ? profile : undefined,
      phone: phone ? phone : undefined,
      technologies: technologies ? technologies : undefined,
      english: english ? english : undefined,
      salary: salary ? salary : undefined,
      status: status ? status : undefined,
      source: source ? source : undefined,
      comment: comment ? comment : undefined,
      author_id: author_id
    };
    console.log(registeredUser);
    const { error } = Joi.validate(registeredUser, this.registrationSchema);
    console.log(error);
    if (error) {
      this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
    } else {
      this.props.addProfile(registeredUser, this.props.User.token);
    }
  };

  onEdit = event => {
    event.preventDefault();
    const { projects, profile, phone, technologies, english, status, source, name, position, portfolio, salary, comment, updater_id } = this.state;
    const updatedUser = {
      projects: projects ? projects : undefined,
      name: name,
      position: position ? position : undefined,
      portfolio: portfolio ? portfolio : undefined,
      profile: profile ? profile : undefined,
      phone: phone ? phone : undefined,
      technologies: technologies ? technologies : undefined,
      english: english ? english : undefined,
      salary: salary ? salary : undefined,
      status: status ? status : undefined,
      source: source ? source : undefined,
      comment: comment ? comment : undefined,
      updater_id: updater_id
    };
    const { error } = Joi.validate(updatedUser, this.editSchema);
    if (error) {
      this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
    } else if (this.props.form === 'edit') {
      this.props.updateCurrentProfile(updatedUser, this.props.currentProfile.id);
      this.props.showHide();
      this.clearForm();
    }
  };
  componentDidMount() {
    this.props.GetAllProjects();
    this.props.GetAllTechnologies();
    this.props.GetAllProfiles();
    if (this.props.form === 'edit' && this.props.currentProfile) {
      const { currentProfile } = this.props;
      this.setState({
        name: currentProfile.name,
        profile: currentProfile.profile,
        position: currentProfile.position,
        portfolio: currentProfile.portfolio,
        projects: currentProfile.projects.map(project => {
          return { label: project.title, id: project.id };
        }),
        phone: currentProfile.phone,
        technologies: currentProfile.technologies.map(technology => {
          return { value: technology.title, label: technology.title, id: technology.id };
        }),
        english: currentProfile.english,
        salary: currentProfile.salary,
        status: currentProfile.status,
        source: currentProfile.source,
        comment: this.props.currentProfile.comment
      });
    }
  }
  registrationErrors = errors => {
    let errorsObject = {};
    Object.keys(errors).forEach(errorKey => {
      const errorText = errors[errorKey];
      errorsObject[errorKey] = errorText;
    });
    this.setState({ errors: errorsObject });
  };
  componentDidUpdate(prevProps) {
    const errors = this.props.Error;
    if (!isEqual(prevProps.Error, errors) && !isEmpty(errors)) {
      this.registrationErrors(errors);
    } else if (!isEqual(prevProps.Error, errors) && isEmpty(errors)) {
      this.setState({ errors: {} });
    }
  }
  handleNameValidation = e => {
    const name = e.target.value;
    this.setState({ name: name });
    let validity = this.props.AllProfiles.filter(profile => profile.name.trim().toLowerCase() === name.trim().toLowerCase());
    if (validity.length) {
      e.target.style.color = 'red';
    } else {
      e.target.style.color = 'black';
    }
  };

  render() {
    return (
      <>
        <div className={styles.conteiner}>
          <form
            onSubmit={
              this.props.form === 'edit' ? event => this.onEdit(event) : this.props.form === 'register' ? event => this.onSubmit(event) : null
            }
            className={styles.formContainer}
          >
            <div className={styles.formContainer__item}>
              <h1 className={styles.title}>{this.props.form}</h1>
              <button
                className={styles.xBtn}
                onClick={this.props.form === 'register' ? this.showRegister : this.props.form === 'edit' ? this.props.showHide : null}
              />
            </div>
            {this.props.EditProfileError ? <p>{this.props.EditProfileError}</p> : null}
            <div className={styles.error}>
              {Object.keys(this.state.errors).map(error => (
                <span key={error}>{this.state.errors[error]}</span>
              ))}
            </div>
            <div className={styles.formContainer__item}>
              <input
                className="form-control mb-1"
                type="name"
                placeholder="Name"
                onChange={event => this.handleNameValidation(event)}
                name="name"
                value={this.state.name}
                error={this.state.errors.name}
              />
            </div>
            <div className={styles.formContainer__item}>
              <input
                placeholder="Position"
                className="form-control mb-1"
                type="position"
                onChange={event => this.handleChange(event)}
                name="position"
                value={this.state.position}
                error={this.state.errors.position}
              />
            </div>
            <div className={styles.formContainer__item}>
              <input
                placeholder="Portfolio"
                className="form-control mb-1"
                type="portfolio"
                onChange={event => this.handleChange(event)}
                name="portfolio"
                value={this.state.portfolio}
                error={this.state.errors.portfolio}
              />
            </div>
            <div className={styles.formContainer__item}>
              <input
                placeholder="Profile"
                className="form-control mb-1"
                type="text"
                onChange={event => this.handleChange(event)}
                name="profile"
                value={this.state.profile}
                error={this.state.errors.profile}
              />
            </div>
            <div className={styles.formContainer__item}>
              <label htmlFor="projects" className={styles.label}>
                Projects
              </label>

              <div>
                <CreatableSelect
                  isMulti
                  styles={customStyles}
                  name="projects"
                  error={this.state.errors.projects}
                  onChange={this.handleProjectSelectChange}
                  onCreateOption={this.handleCreateProjectOption}
                  options={this.getProjectOptionsForSelect()}
                  value={this.state.projects}
                  placeholder={<div>Select or create new</div>}
                  getOptionValue={arr => arr.id}
                />
              </div>
            </div>
            <div className={styles.formContainer__item}>
              <input
                placeholder="Phone"
                className="form-control mb-1"
                type="text"
                onChange={event => this.handleChange(event)}
                name="phone"
                value={this.state.phone}
                error={this.state.errors.phone}
              />
            </div>
            <div className={styles.formContainer__item}>
              <label htmlFor="technologies" className={styles.label}>
                Technologies
              </label>
              <div>
                <CreatableSelect
                  isMulti
                  styles={customStyles}
                  name="technology"
                  error={this.state.errors.technology}
                  onChange={this.handleTechnologiesSelectChange}
                  onCreateOption={this.handleCreateTechnologyOption}
                  options={this.getTechnologiesOptionsForSelect()}
                  value={this.state.technologies}
                  placeholder={<div>Select or create new</div>}
                  getOptionValue={arr => arr.id}
                />
              </div>
            </div>

            <div className={styles.formContainer__item}>
              <select
                type="text"
                className="form-control mb-1"
                onChange={event => this.handleChange(event)}
                name="english"
                value={this.state.english}
                error={this.state.errors.english}
              >
                <option value={''}>English</option>
                <option type="checkbox" value="no english" checked>
                  no english
                </option>
                <option type="checkbox" value="good" checked>
                  good
                </option>
                <option type="checkbox" value="fluent" checked>
                  fluent
                </option>
              </select>
            </div>

            <div className={styles.formContainer__item}>
              <input
                placeholder="Salary Range"
                className="form-control mb-1"
                type="text"
                onChange={event => this.handleChange(event)}
                name="salary"
                value={this.state.salary}
                error={this.state.errors.minSalary}
              />
            </div>

            <div className={styles.formContainer__item}>
              <select
                className="form-control mb-1"
                type="text"
                value={this.state.status}
                onChange={event => this.handleChange(event)}
                name="status"
                error={this.state.errors.status}
              >
                <option value={''}>Status</option>
                <option type="checkbox" value="wrote on linkedin" checked>
                  linkedin
                </option>

                <option type="checkbox" value="black_list" checked>
                  blacklist
                </option>
                <option type="checkbox" value="interested" checked>
                  interested
                </option>
                <option type="checkbox" value="rejected" checked>
                  rejected
                </option>
                <option type="checkbox" value="shortlisted" checked>
                  shortlisted
                </option>
                <option type="checkbox" value="hired" checked>
                  hired
                </option>
              </select>
            </div>

            <div className={styles.formContainer__item}>
              <select
                type="text"
                className="form-control mb-1"
                onChange={event => this.handleChange(event)}
                name="source"
                value={this.state.source}
                error={this.state.errors.source}
              >
                <option value={''}>Source</option>
                <option type="checkbox" value="refference" checked>
                  refference
                </option>
                <option type="checkbox" value="linkedin" checked>
                  linkedin
                </option>

                <option type="checkbox" value="job post" checked>
                  job post
                </option>
              </select>
            </div>

            <div className={styles.formContainer__item}>
              <div className="form-control mb-1">
                <input
                  placeholder="Comment"
                  className={styles.widthMax}
                  type="text"
                  onChange={event => this.handleChange(event)}
                  name="comment"
                  value={this.state.comment}
                  error={this.state.errors.fromdateRange}
                />
              </div>
            </div>

            <div className={styles.formContainer__item}>
              <button type="submit" className="btn btn-success">
                {this.props.form}
              </button>
              <button className="btn btn-warning" onClick={this.clearForm}>
                Clear
              </button>
            </div>
          </form>
        </div>
      </>
    );
  }
}

export default Registration;
