import * as types from '../constants/projectTypes';

const initState = {
  AllProjects: [],
  Error: {},
  CreateProjectErrors: []
};

const ProjectReducer = (state = initState, action) => {
  switch (action.type) {
    case types.GET_ALL_PROJECTS:
      return {
        ...state,
        AllProjects: action.data
      };
    case types.CREATE_PROJECT:
      console.log('action recieved in reducer ', action.data);
      return {
        ...state,
        AllProjects: [...state.AllProjects, action.data]
      };

    case types.EDIT_PROJECT:
      console.log('action recieved in reducer ', action.data);
      return {
        ...state,
        AllProjects: [...state.AllProjects, action.data]
      };

    case types.DELETE_PROJECT:
      let filteredProjects = state.AllProjects.filter(item => item.id !== action.id);
      return {
        ...state,
        AllProjects: filteredProjects
      };

    case types.CREATE_PROJECT_ERRORS:
      return {
        CreateProjectErrors: action.data
      };

    case types.DELETE_PROJECT_ERRORS:
      return {
        DeleteProjectError: action.data
      };
    default:
      return state;
  }
};

export default ProjectReducer;
