import React, { Component } from 'react';

import CreateUserView from '../../../../containers/CreateUserView';
import Button from '../../../common/Button/Button';
import styles from './ManageUser.module.css';
import PermissionRuler from '../../../../hoc/PermissionRuler';
import Gravatar from 'react-gravatar';
import CreateIcon from '../../../common/Table/CreateIcon';

class ManageUser extends Component {
  state = {
    show: false,
    user: {}
  };
  componentDidMount() {
    this.props.GetAllUsers();
  }

  handleShowHide = e => {
    e.preventDefault();
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };
  handleShowErrors = () => {
    return this.props.Error ? (
      <p>
        {this.props.Error.map((err, i) => {
          return <p key={i}>{err}</p>;
        })}{' '}
      </p>
    ) : null;
  };
  handleDeleteUser = user => {
    this.props.DeleteUser(user.id);
  };

  handleEditUser = user => {
    this.props.EditUser(user);
    this.setState({
      show: !this.state.show
    });
  };

  render() {
    return (
      <>
        <div className={styles.createBox}>
          <PermissionRuler roles={['admin']}>
            <Button className="button" onClick={this.handleShowHide}>
              Create User &nbsp;&nbsp; <CreateIcon />
            </Button>
            <CreateUserView show={this.state.show} hide={this.handleShowHide} />
          </PermissionRuler>
        </div>
        <div className={styles.conteiner}>
          <div className="col-md-12">
            <div className="card table-card">
              <div className="card-header">
                <h5>Users</h5>
              </div>
              <div className="card-block p-b-0">
                <div className="table-responsive">
                  <table className="table table-hover m-b-0">
                    <thead>
                      <tr>
                        <th>Gravatar</th>
                        <th>Name</th>
                        <th>Email</th>
                        <th>Role</th>
                        <th>Edit</th>
                        <th>Delete</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.props.AllUsers
                        ? this.props.AllUsers.map((user, i) => {
                            return (
                              <tr key={i}>
                                <td>
                                  <Gravatar email={user.email} size={50} rating="pg" default="monsterid" className={styles.img} />
                                </td>
                                <td>{user.name}</td>
                                <td>{user.email}</td>
                                <td>{user.role}</td>
                                <td>
                                  <button className="btn btn-warning" onClick={() => this.handleEditUser(user)}>
                                    Edit
                                  </button>
                                </td>
                                <td>
                                  <button className="btn btn-danger" onClick={() => this.handleDeleteUser(user)}>
                                    Delete
                                  </button>
                                </td>
                              </tr>
                            );
                          })
                        : null}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default ManageUser;
