import React, { Component } from 'react';
import ReactTable from 'react-table';
import { withRouter } from 'react-router-dom';

import 'react-table/react-table.css';
import './Table.css';
import ExportExcel from '../../Home/Export/ExportExcel';
import ExportPdf from '../../Home/Export/ExportPdf';
import SVG from './FilterIcon';
import ClearIcon from './ClearIcon';
import CreateIcon from './CreateIcon';
import ExportSvg from './Export';
import Button from '../Button/Button';
import styles from './Table.module.css';
import PermissionRuler from '../../../hoc/PermissionRuler';
import Filter from '../../../containers/FilterFormView';
import RegistrationFormView from '../../../containers/RegistrationFormView';
import { isArray } from 'util';
import { Link } from 'react-router-dom';
import ExportFile from './ExportFile';
class Table extends Component {
  state = {
    show: false,
    roughcolumns: [],
    columnHeaders: JSON.parse(localStorage.getItem('tableColumns')) || this.props.columnHeaders
  };

  componentDidMount() {
    if (!localStorage.getItem('tableColumns')) {
      localStorage.setItem('tableColumns', JSON.stringify(this.props.columnHeaders));
    }
  }

  getElementCoords = id => {
    let coorX0 = document.getElementById(id).getBoundingClientRect().left;
    let coorX2 = coorX0 + document.getElementById(id).offsetWidth;
    let coorX1 = (coorX0 + coorX2) / 2;

    return [coorX0, coorX1, coorX2];
  };

  placeBefore = (movingItem, origin) => {
    let movingObject = this.state.columnHeaders.find(x => x.name === movingItem);
    if (movingItem !== origin) {
      let currentArray = this.state.columnHeaders.filter(x => x.name !== movingItem);
      let filteredDown = currentArray.filter(x => currentArray.indexOf(x) < currentArray.indexOf(currentArray.find(x => x.name === origin)));
      let filteredUp = currentArray.filter(x => currentArray.indexOf(x) >= currentArray.indexOf(currentArray.find(x => x.name === origin)));

      this.setState({
        roughcolumns: [...filteredDown, movingObject, ...filteredUp]
      });
    }
  };
  placeAfter = (movingItem, origin) => {
    let movingObject = this.state.columnHeaders.find(x => x.name === movingItem);
    if (movingItem !== origin) {
      let currentArray = this.state.columnHeaders.filter(x => x.name !== movingItem);
      let filteredDown = currentArray.filter(x => currentArray.indexOf(x) <= currentArray.indexOf(currentArray.find(x => x.name === origin)));
      let filteredUp = currentArray.filter(x => currentArray.indexOf(x) > currentArray.indexOf(currentArray.find(x => x.name === origin)));

      this.setState({
        roughcolumns: [...filteredDown, movingObject, ...filteredUp]
      });
    }
  };

  handleDragging = e => {
    let coordinates = [];
    this.state.columnHeaders.map(header => {
      coordinates.push([header.name, ...this.getElementCoords(header.name)]);
      return 0;
    });
    coordinates.map(header => {
      if (e.clientX === 0) {
      } else if (e.clientX < header[3] && e.clientX > header[2]) {
        return this.placeAfter(e.target.id, coordinates[coordinates.indexOf(header)][0]);
      } else if (e.clientX < header[2] && e.clientX > header[1]) {
        return this.placeBefore(e.target.id, coordinates[coordinates.indexOf(header)][0]);
      }
      return 0;
    });
  };

  makeItString = a => {
    if (isArray(a)) {
      return a.map(item => {
        return '#' + item.title + ' ';
      });
    }
    return a;
  };

  hoverModal = id => {
    const FiltUser = this.props.rows.filter(row => row.id === id);
    this.setState({ hoveruser: FiltUser[0] });
  };
  hoverLeave = () => {
    this.setState({ hoveruser: false });
  };

  headerRow = () => {
    let headers = this.state.columnHeaders.map(header => {
      return (
        <th
          key={header.title}
          id={header.name}
          draggable="true"
          onDrag={this.handleDragging}
          onDragEnd={() => {
            this.setState({ columnHeaders: this.state.roughcolumns });
            localStorage.setItem('tableColumns', JSON.stringify(this.state.roughcolumns));
          }}
        >
          {header.title}
        </th>
      );
    });
    return <tr>{headers}</tr>;
  };
  contentRows = () => {
    if (!this.props.rows) {
      return (
        <tbody>
          <tr>
            <td colSpan={this.state.columnHeaders.length} className={styles.noItemsAlert}>
              No Items To Show
            </td>
          </tr>
        </tbody>
      );
    }
    let rows = this.props.rows.map(row => {
      let currentrow = this.state.columnHeaders.map(header => {
        return (
          <td key={header.name}>
            <div className={styles.transBox}>
              <Link to={`${row.id}`}>{this.makeItString(row[header.name])}</Link>
            </div>
          </td>
        );
      });
      return (
        <tr
          key={this.props.rows.indexOf(row)}
          className={(row.status === 'black_list' && styles.black_listed) || (row.status === 'shortlisted' && styles.shortlisted) || styles.smth}
        >
          {currentrow}
        </tr>
      );
    });
    return <tbody>{rows}</tbody>;
  };

  showFilter = () => {
    this.props.toggleFilter();
    this.props.toggleRegister(false);
  };
  showRegister = () => {
    this.props.toggleFilter(false);
    this.props.toggleRegister();
  };

  handleShowHide = e => {
    e.preventDefault();
    this.setState(currentState => ({
      show: !currentState.show
    }));
  };
  GetAllProfiles = () => {
    this.props.GetAllProfiles();
  };

  render() {
    return (
      <>
        {!this.props.onProjectDetail && (
          <div className={styles.flexBtns}>
            <PermissionRuler roles={['admin', 'manager']}>
              <Button className="button" onClick={() => this.showRegister()}>
                Create Profile &nbsp;&nbsp; <CreateIcon />
              </Button>
            </PermissionRuler>
            <Button className="button" onClick={() => this.showFilter()}>
              Filter &nbsp;&nbsp; <SVG />
            </Button>

            <Button className="button" onClick={() => this.GetAllProfiles()}>
              Clear Filter &nbsp;&nbsp;
              <ClearIcon />
            </Button>

            <div className="exportBox">
              <ExportFile />

              <div
                className="exportBox__buttons"
                style={{
                  transform: this.state.show ? 'translateY(0vh)' : 'translateY(-20vh)',
                  opacity: this.state.show ? '1' : '0'
                }}
              >
                <ExportFile />
              </div>
            </div>
          </div>
        )}

        <div className={styles.container}>
          <table className={styles.smartTable} id="html_to_excel">
            <thead>{this.headerRow()}</thead>
            {this.contentRows()}
          </table>
        </div>
      </>
    );
  }
}
export default withRouter(Table);
