import { connect } from 'react-redux';
import Technologies from '../components/Home/ContentPages/Technologies/Technologies';
import { GetAllTechnologies, DeleteTechnology } from '../actions/TechnologyActions';
// import {TechnologiesReducer} from '../reducers/TechnologiesReducer';

const mapDispatchToProps = dispatch => {
  return {
    GetAllTechnologies: () => {
      dispatch(GetAllTechnologies());
    },
    DeleteTechnology: id => {
      dispatch(DeleteTechnology(id));
    }
  };
};

const mapStateToProps = state => {
  return {
    AllTechnologies: state.TechnologyReducer.AllTechnologies
  };
};

const TechnologiesView = connect(
  mapStateToProps,
  mapDispatchToProps
)(Technologies);

export default TechnologiesView;
