import React, { Component } from 'react';
import Table from '../../../../common/Table/Table';
import styles from './TechnologyItem.module.css';

class TechnologyItem extends Component {
  componentDidMount() {
    this.props.GetAllTechnologies();
  }

  render() {
    const myTechnology = this.props.AllTechnologies.find(item => {
      return parseInt(item.id) === parseInt(this.props.match.params.id);
    });
    if (myTechnology) {
      var data = [...myTechnology.profiles];
    }
    return (
      <div>
        <Table className={styles.table} rows={data} />
      </div>
    );
  }
}

export default TechnologyItem;
