import { connect } from 'react-redux';
import { GetAllProjects, CreateProject } from '../actions/ProjectActions';
import { GetAllTechnologies, CreateTechnology } from '../actions/TechnologyActions';
import { GetAllProfiles, addProfile, toggleRegister, toggleFilter, updateCurrentProfile } from '../actions/ProfilePageActions';

import Registration from '../components/Home/Filter/RegistrationForm';

const mapStateToProps = state => {
  return {
    AllProfiles: state.UserReducer.AllProfiles,
    User: state.UserReducer.User,
    Authenticated: state.UserReducer.Authenticated,
    AllProjects: state.ProjectReducer.AllProjects,
    AllTechnologies: state.TechnologyReducer.AllTechnologies,
    Error: state.UserReducer.Error,
    showRegister: state.UserReducer.showRegister,
    showFilter: state.UserReducer.showFilter,
    CurrentProfile: state.UserReducer.CurrentProfile,
    EditProfileError: state.UserReducer.EditProfileError
  };
};

const mapDispatchToProps = dispatch => ({
  addProfile: (newUser, token) => {
    dispatch(addProfile(newUser, token));
  },
  updateCurrentProfile: (currentUser, token, id) => {
    dispatch(updateCurrentProfile(currentUser, token, id));
  },
  GetAllProfiles: token => {
    dispatch(GetAllProfiles(token));
  },
  GetAllProjects: () => {
    dispatch(GetAllProjects());
  },
  GetAllTechnologies: () => {
    dispatch(GetAllTechnologies());
  },
  toggleRegister: status => {
    dispatch(toggleRegister(status));
  },
  toggleFilter: status => {
    dispatch(toggleFilter(status));
  },
  createProject: (project, token) => {
    dispatch(CreateProject(project, token));
  },
  createTechnology: (technology, token) => {
    dispatch(CreateTechnology(technology, token));
  }
});
const RegFilterForm = connect(
  mapStateToProps,
  mapDispatchToProps
)(Registration);

export default RegFilterForm;
