import {connect} from 'react-redux';
import TechnologyItem from '../components/Home/ContentPages/Technologies/TechnologyItem/TechnologyItem';
import {GetAllTechnologies} from '../actions/TechnologyActions';

const mapDispatchToProps = (dispatch) => {
    return {
        GetAllTechnologies: () => {dispatch(GetAllTechnologies)}
    }
}

const mapStateToProps = (state) => {
    return {
        AllTechnologies: state.TechnologyReducer.AllTechnologies
    }
}


const TechnologyItemView = connect(mapStateToProps,mapDispatchToProps)(TechnologyItem)

export default TechnologyItemView;