import axios from '../utils/axios';
import * as types from '../constants/projectTypes';

export const GetAllProjects = () => {
  return async dispatch => {
    try {
      let Projects = await axios.get('get-projects');
      dispatch({ type: types.GET_ALL_PROJECTS, data: Projects.data.projects });
    } catch (err) {
      console.log(err);
    }
  };
};

