export const CreateProject = newProj => {
  return async dispatch => {
    let projectData = { ...newProj };
    console.log(' project data from action ', projectData);
    try {
      let addProject = await axios.post('/store-project', { ...projectData });
      console.log('inside try block ', addProject);
      dispatch({ type: types.CREATE_PROJECT, data: addProject.data.project });
    } catch (err) {
      dispatch({ type: types.CREATE_PROJECT_ERRORS, data: 'something went wrong, try again later' });
      console.log(err);
    }
  };
};

export const EditProject = (updatedProj, id) => {
  return async dispatch => {
    let projectData = { ...updatedProj };
    console.log(' project data from action ', projectData);
    try {
      let editProject = await axios.put(`/update-project/${id}`, { ...projectData });
      console.log('inside try block ', editProject);
      let Projects = await axios.get('get-projects');
      dispatch({ type: types.GET_ALL_PROJECTS, data: Projects.data.projects });
    } catch (err) {
      console.log(err);
    }
  };
};

export const DeleteProject = id => {
  return async dispatch => {
    try {
      await axios.delete(`/destroy-project/${id}`);
      dispatch({ type: types.DELETE_PROJECT, id: id });
    } catch (err) {
      dispatch({ type: types.DELETE_PROJECT_ERRORS, id: 'something went wrong, try again later' });
      console.log(err);
    }
  };
};
