import axios, { setToken, clearToken } from '../utils/axios';
import * as types from '../constants/loginTypes';

export const UserLogin = user => {
  return async dispatch => {
    return await axios.post('login', user).then(
      response => {
        let {
          data: { user, success }
        } = response;

        let receivedUser = {
          name: user.name,
          token: success.token,
          role: user.role,
          id: user.id,
          status: true,
        };

        setToken(success.token);
        dispatch({ type: types.USER_LOGIN, datas: receivedUser });

        return receivedUser;
      },
      error => {
        let datas = {
          msg: 'you entered wrong datas',
          status: false
        };
        dispatch({ type: types.WRONG_USER, datas: datas });

        return Promise.reject(error);
      }
    );
  };
};

export const setUser = user => {
  return async dispatch => {
    setToken(user.token);
    dispatch(setUserActionCreator(user));
  };
};

export const setUserActionCreator = user => {
  return {
    type: types.SET_USER,
    user: user
  };
};

export const UserLogout = () => {
  return async dispatch => {
    clearToken();
    dispatch(UserLogoutActionCreator());
  };
};

export const UserLogoutActionCreator = () => {
  return {
    type: types.USER_LOGOUT
  };
};

export const userFalse = () => {
  return {
    type: types.USER_FALSE
  };
};
