import React from 'react';
import ReactDOM from 'react-dom';
import * as serviceWorker from './serviceWorker';
import { BrowserRouter } from 'react-router-dom';
import './index.css';
import App from './App';

import {Provider} from 'react-redux';
import {createStore, applyMiddleware, combineReducers , compose} from 'redux';
import thunk from 'redux-thunk';

import UserReducer  from './reducers/UserReducer';
import ProjectReducer from './reducers/ProjectReducer';
import TechnologyReducer from './reducers/TechnologyReducer';
import AdminReducer from './reducers/AdminReducer';
import ManageUserReducer from './reducers/ManageUserReducer';

const rootReducer = combineReducers({
    UserReducer:UserReducer,
    ProjectReducer:ProjectReducer,
    TechnologyReducer:TechnologyReducer,
    AdminReducer: AdminReducer,
    ManageUserReducer:ManageUserReducer
})
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(rootReducer,composeEnhancers(applyMiddleware(thunk)));
ReactDOM.render(<Provider store={store}><BrowserRouter> <App /></BrowserRouter></Provider>,
document.getElementById('root'));

serviceWorker.unregister();