import { connect } from 'react-redux';
import ManageUser from '../components/Home/ContentPages/ManageUser/ManageUser';
import { DeleteUser, GetAllUsers, EditUser } from '../actions/ManageUsersActions';

const mapDispatchToProps = dispatch => {
  return {
    GetAllUsers: () => {
      dispatch(GetAllUsers());
    },
    DeleteUser: id => {
      dispatch(DeleteUser(id));
    },
    EditUser: user => {
      dispatch(EditUser(user));
    }
  };
};

const mapStateToProps = state => {
  return {
    AllUsers: state.ManageUserReducer.AllUsers,
    Error: state.ManageUserReducer.Error,
    UpdatingUser: state.ManageUserReducer.UpdatingUser
  };
};

const ManageUserView = connect(
  mapStateToProps,
  mapDispatchToProps
)(ManageUser);

export default ManageUserView;
