import React, { Component } from 'react';
import Joi from '@hapi/joi';
import isEmpty from 'lodash.isempty';

import styles from './CreateUser.module.css';

class CreateUser extends Component {
  state = {
    email: '',
    name: '',
    password: '',
    c_password: '',
    role: 'viewer',
    errors: {}
  };

  userSchema = {
    email: Joi.string()
      .min(8)
      .required()
      .label('email')
      .max(120),
    name: Joi.string()
      .min(8)
      .required()
      .label('name')
      .max(120),

    password: Joi.string()
      .required()
      .label('password')
      .min(8)
      .max(32),
    c_password: Joi.string()
      .required()
      .label('c_password')
      .min(8)
      .max(32)
  };
  editUserSchema = {
    email: Joi.string()
      .min(8)
      .required()
      .label('email')
      .max(120),
    name: Joi.string()
      .min(8)
      .required()
      .label('name')
      .max(120),
    role: Joi.string()
      .label('role')
      .max(120)
      .optional(),
    password: Joi.string().optional(),
    c_password: Joi.string().optional()
  };
  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onSubmit = event => {
    event.preventDefault();
    const { email, password, c_password, name } = this.state;
    let user = {
      name: name,
      email: email,
      password: password,
      c_password: c_password
    };
    const { error } = Joi.validate(user, this.userSchema);
    if (error) {
      this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
    } else {
      this.props.userRegistration(user);
      this.setState({
        name: '',
        email: '',
        role: '',
        password: '',
        c_password: '',
        errors: ''
      });
      this.props.hide(event);
    }
  };
  onEdit = (event, id) => {
    event.preventDefault();
    const { email, name, role, password, c_password } = this.state;
    if (password && c_password) {
      let user = { name: name, email: email, role: role, password: password, c_password: c_password };
      const { error } = Joi.validate(user, this.editUserSchema);
      if (error) {
        this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
      } else {
        this.props.userFinalUpdate(user, id);
        this.setState({
          name: '',
          email: '',
          role: '',
          password: '',
          c_password: '',
          errors: ''
        });
        this.props.hide(event);
        // this.props.userUpdateCancel();
      }
    } else {
      let user = { name: name, email: email, role: role };
      const { error } = Joi.validate(user, this.editUserSchema);
      if (error) {
        this.setState({ errors: { [error.details[0].path[0]]: error.details[0].message } });
      } else {
        this.props.userFinalUpdate(user, id);
        this.setState({
          name: '',
          email: '',
          role: '',
          errors: ''
        });
        this.props.hide(event);
        // this.props.userUpdateCancel();
      }
    }
  };

  onCancel = event => {
    event.preventDefault();
    this.setState({
      name: '',
      email: '',
      role: '',
      password: '',
      c_password: '',
      errors: ''
    });
    this.props.userUpdateCancel();
    this.props.hide(event);
  };

  componentDidUpdate(prevProps) {
    if (this.props.UpdatingUser && prevProps.UpdatingUser !== this.props.UpdatingUser) {
      const { name, email, role } = this.props.UpdatingUser;
      this.setState({
        name: name || '',
        email: email || '',
        role: role || '',
        password: '',
        c_password: ''
      });
    }
  }
  render() {
    return (
      <div
        className={styles.modalWrapper}
        style={{
          opacity: this.props.show ? '1' : '0',
          zIndex: this.props.show ? '9999' : '0'
        }}
      >
        <form onSubmit={isEmpty(this.props.UpdatingUser) ? event => this.onSubmit(event) : event => this.onEdit(event, this.props.UpdatingUser.id)}>
          <div className={styles.cont}>
            {this.props.Error ? <p>{this.props.Error} </p> : null}

            <input className="form-control" type="text" name="email" value={this.state.email} placeholder="Email" onChange={this.onChange} />
            {this.state.errors.email ? <p> {this.state.errors.email} </p> : null}
          </div>
          <div className={styles.cont}>
            <input className="form-control" type="text" name="name" value={this.state.name} placeholder="Name" onChange={this.onChange} />
            {this.state.errors.name ? <p> {this.state.errors.name} </p> : null}
          </div>

          <div className={styles.cont}>
            <input
              className="form-control"
              type="password"
              name="password"
              placeholder="Enter Password"
              value={this.state.password}
              onChange={this.onChange}
            />
            {this.state.errors.password ? this.state.errors.password : null}
          </div>

          <div className={styles.cont}>
            <input
              className="form-control"
              type="password"
              name="c_password"
              placeholder="Repeat Password"
              value={this.state.c_password}
              onChange={this.onChange}
            />
            {this.state.errors.repeatpassword ? this.state.errors.repeatpassword : null}
          </div>

          {isEmpty(this.props.UpdatingUser) ? null : (
            <div className={`${styles.cont} ${styles.margin}`}>
              <label htmlFor="role" className={styles.CreateUser_label}>
                Role
              </label>
              <select name="role" value={this.state.role} onChange={this.onChange}>
                <option value="admin">admin</option>
                <option value="manager">manager</option>
                <option value="viewer">viewer</option>
              </select>
              {this.state.errors.role ? this.state.errors.role : null}
            </div>
          )}

          <div className={styles.cont_last_child}>
            <button type="submit" className="btn btn-success">
              Submit
            </button>
            <button type="reset" onClick={event => this.onCancel(event)} className="btn btn-danger">
              Cancel
            </button>
          </div>
        </form>
      </div>
    );
  }
}

export default CreateUser;
