import * as types from '../constants/manageUsersTypes';

const initState = {
  AllUsers: [],
  Error: [],
  UpdatingUser: {}
};

const ManageUserReducer = (state = initState, action) => {
  switch (action.type) {
    case types.GET_ALL_USERS:
      return {
        ...state,
        AllUsers: action.data
      };
    case types.CREATE_USER:
      return {
        ...state,
        AllUsers: [...state.AllUsers, action.data]
      };

    case types.CREATE_USER_ERROR:
      return {
        ...state,
        Error: [...state.Error, action.data]
      };

    case types.UPDATE_USER:
      return {
        ...state,
        UpdatingUser: action.data
      };
    case types.FINAL_UPDATE_USER:
      return {
        ...state,
        AllUsers: action.data,
        UpdatingUser: undefined
      };
    case types.FINAL_UPDATE_USER_ERROR:
      return {
        ...state,
        Error: action.data,
        UpdatingUser: undefined
      };

    case types.USER_UPDATE_CANCEL:
      return {
        ...state,
        UpdatingUser: undefined
      };

    case types.DELETE_USERS_ERROR:
      return {
        ...state,
        Error: [...state.Error, action.data]
      };

    default:
      return state;
  }
};

export default ManageUserReducer;
