import React, { Component } from 'react';
import ReactHTMLTableToExcel from 'react-html-table-to-excel';

import './Export.css';

class ExportExcel extends Component {
  render() {
    return (
      <div >
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className='export'
          table="table-to-xls"
          filename="Profiles"
          sheet="tablexls"
          buttonText="Excel"
        />
        <table hidden={true} id="table-to-xls">
          <thead>
            <tr>
              <th>Project</th>
              <th>Profile</th>
              <th>Position</th>
              <th>Phone</th>
              <th>LinkedIn</th>
              <th>Portfolio</th>
              <th>Comment</th>
              <th>Technologies</th>
              <th>English</th>
              <th>Expected Salary</th>
              <th>Source</th>
              <th>Status</th>
              <th>Date</th>
            </tr>
          </thead>
          <tbody>
            {this.props.profiles.map(profile => {
              return (
                <tr key={profile.id}>
                  <td>{profile.projects}</td>
                  <td>{profile.name}</td>
                  <td>{profile.position}</td>
                  <td>{profile.phone}</td>
                  <td>{profile.profile}</td>
                  <td>{profile.portfolio}</td>
                  <td>{profile.comment}</td>
                  <td>{profile.technologies}</td>
                  <td>{profile.english}</td>
                  <td>{profile.salary}</td>
                  <td>{profile.source}</td>
                  <td>{profile.status}</td>
                  <td>{profile.created_at}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    );
  }
}

export default ExportExcel;
