import React from 'react';
import { Route, BrowserRouter } from 'react-router-dom';
import styles from './Home.module.css';
import AnalyticsView from '../../containers/AnalyticsView';
import Calendar from './ContentPages/Calendar/Calendar';
import ProfileListView from '../../containers/ProfileListView';
import ProjectView from '../../containers/ProjectsView';
import ProjectItemView from '../../containers/ProjectItemView';
import TechnologiesView from '../../containers/TechnologiesView';
import TechnologyItemView from '../../containers/TechnologyItemView.jsx';
import SidebarView from '../../containers/SidebarView.jsx';
import ManageUserView from '../../containers/ManageUserView';
import ProfileItemView from '../../containers/ProfileItemView';
import DashboardView from '../../containers/DashboardView';
import { Switch } from 'react-router-dom';
import AddProfilesView from '../../containers/AddProfilesView';

class Home extends React.Component {
  getAllUsers = () => {
    this.props.getAllUsers();
  };
  render() {
    // const { path } = this.props.match;
    return (
      <>
        <BrowserRouter>
          <main className={styles.main}>
            <SidebarView />
            <div className={styles.content}>
              <Switch>
                <Route path={`/home/technologies/:id`} exact component={TechnologyItemView} />
                <Route path={`/home/analytics`} exact component={AnalyticsView} />
                <Route path={`/home/project/:id`} exact component={ProjectItemView} />
                <Route path={`/home/calendar`} exact component={Calendar} />
                <Route path={`/home/manage-user`} exact component={ManageUserView} />
                <Route path={`/home/projects`} exact component={ProjectView} />
                <Route path={`/home/technologies`} exact component={TechnologiesView} />
                <Route path={`/home/dashboard`} exact component={DashboardView} />
                <Route path={`/home/add-profiles`} exact component={AddProfilesView} />
                <Route path={`/home/:id`} exact component={ProfileItemView} />
                <Route path={`/home`} exact component={ProfileListView} />
              </Switch>
            </div>
          </main>
        </BrowserRouter>
      </>
    );
  }
}
export default Home;
